.body{
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  background-image: url('https://aminus3.s3.amazonaws.com/image/g0034/u00033491/i01964949/a3034f060eadb06a3f580cbfc58e221a_large.jpg');
  background-position: center;
  height: 20vh;
  margin: -5px;
}

.body a {
  margin-right: 40px;
  text-decoration: none;
  font-size: x-large;
  color: white;
  background-color: black;
  border-radius: 10px;
  cursor: pointer;
}

@media screen and (max-width: 700px) {
  .body a{
    font-size: large;
    margin-right: 10px;
  }
}