.body{
  padding: 20px;
}

.blueGrid{
  display: grid;
  border: 5px solid blue;
}

.avatar{
  width: 100%;
  height: 40vh;
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin-top: 10vh;
border-bottom: 1px solid orange;
}

.avatar img{
  width: 100px;
  border-radius: 10px;
}

.textArea{
  margin-top: 10vh;
  border: 5px solid black;
  width: 100%;
  height: 20vh;
}
.inputDiv button{
  padding: 5px 50px;
}

.header{
  margin-top: 10vh;
  border: 3px solid orange;
  padding: 10px;
}

.header a{
  text-decoration: none;
}

.textHeader{
  width: 100%;
  height: 6vh;
  display: flex;
  justify-content: space-between;
  border-bottom: 2px solid red;
}

.textAvatar{
  display: flex;
  align-items: center;
  gap: 8px;
}
.textAvatar img{
  width: 5vh;
  border-radius: 10px;
}
.textAvatar a{
  text-decoration: none;
  color: white;
}

.date{
  display: flex;
  font-size: small;
  margin-right: 10px;
}

.textEdit{
  cursor: pointer;
}


.green{
  color: green;
}

.textText {
  overflow: scroll;
}

.dataAndIcons{
  display: flex;
  align-items: center;
}