  color: white;
  background-color: #232f3e;
}

#d-f{
  display: flex;
}
#a-c{
  display: flex;
  align-items: center;
}
#j-a{
  display: flex;
  align-items: center;
  justify-content: space-around;
}


#button{
  width: 100px;
  height: 40px;
}

/* LOADING  */
#loading {
  min-height: 5vh;
  background-image: linear-gradient(
    to right,
    white,
    silver,
    black
  );
  background-size: 200% 100%;
  animation: anim 1s linear infinite;
}

@keyframes anim{
  from{
      background-position: 50%;
    }
    to {
      background-position: -150%;
    }
}