.body textarea{
  width: 98%;
  height: 15vh;
  font-size: larger;
}

.post {
  margin: 40px 1px;
  background-color: darkgreen;
}

.text {
  height: 60vh;
  display: flex;
  background-color: silver;
}

.textText{
  padding: 0 10px;
}

.user {
  width: 20%;
  height: 100%;
  display: grid;
  justify-content: center;
  background-color: red;
}

.user img{
  width: 100%;
  border: 4px solid orange;
  box-sizing: border-box;
}

.header {
  height: 3vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.emoji{
  cursor: pointer;
  font-size: x-large;
}

.page{
  margin: 30px 0;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: green;
}