
.body{
  padding: 40px;
  min-height: 70vh;
  background-color: white;
  color: black;
}

.des{
  display: flex;
  justify-content: space-around;
  background-color: rgb(215, 215, 215);

}

.des input{
  padding: 30px;
}

.desP{
  min-width: 150px;
  border: 1px solid orange;
}

.avatars{
  display: flex;
  flex-wrap: wrap;
  background-color: rgb(215, 215, 215);

}
.grey{
  background-color: rgb(215, 215, 215);
  
}

.avatarImg{
  width: 100px;
  height: 100px;
  display: flex;
  flex-wrap: wrap;
  object-fit: cover;
  margin: 10px;
  border-radius: 10px;
  cursor: pointer;
}